import React, { useState } from "react"
import { useStaticQuery, graphql } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"

import Fade from "react-reveal/Fade"

import Modal from "../general/modal"

import * as videosStyles from "../../styles/videos.module.scss"
import * as layoutStyles from "../../styles/layout.module.scss"

export default function Videos() {
  const data = useStaticQuery(graphql`
    query Videos {
      allMarkdownRemark(
        sort: { fields: frontmatter___updated_at }
        filter: { fileAbsolutePath: { regex: "/(index)/(videos)/" } }
      ) {
        edges {
          node {
            frontmatter {
              description
              title
              video_link
              image {
                childImageSharp {
                  gatsbyImageData
                }
              }
            }
          }
        }
      }
    }
  `)

  const [showModal, setShowModal] = useState(false)
  const [videoSrc, setVideoSrc] = useState("")
  const [videoTitle, setVideoTitle] = useState("")

  const toggleModal = e => {
	const src = e.target.value
	// console.log("SRC",src);
	const [video, title] = src.split(",");
    if (!showModal) {
	  setVideoSrc(video)
	  setVideoTitle(title)
    } else {
	  setVideoSrc("")
	  setVideoTitle("")
    }
    setShowModal(!showModal)
  }

  const videos = data.allMarkdownRemark.edges
//   console.log(videos)
  const posts = videos.map((p, index) => (
    <Fade
      key={index}
      left={index % 2 === 0}
      right={index % 2 === 1}
      distance={"100px"}
      fraction={0.5}
    >
      <div
        className={videosStyles.rowWrapper}
        style={index % 2 === 1 ? { flexDirection: "row-reverse" } : {}}
      >
        <div className={videosStyles.colWrapper}>
          <h3 className={videosStyles.videoTitle}>
            {p.node.frontmatter.title}
          </h3>
          <p className={videosStyles.videoDescription}>
            {p.node.frontmatter.description}
          </p>
          <button
            value={`${p.node.frontmatter.video_link},${p.node.frontmatter.title}`}
            onClick={toggleModal}
            className={`${layoutStyles.btn} ${videosStyles.btn}`}
          >
            Watch Video
          </button>
          {showModal ? (
            <Modal
              closeModal={toggleModal}
              title={videoTitle}
              media={videoSrc}
            />
          ) : (
            <div></div>
          )}
        </div>
        <div className={videosStyles.colWrapper}>
          <GatsbyImage
            image={p.node.frontmatter.image.childImageSharp.gatsbyImageData}
            alt="Video image"
          />
        </div>
      </div>
    </Fade>
  ))

  return <div className={videosStyles.container}>{posts}</div>
}
